"use client"
import { useState } from 'react';
import { motion } from 'framer-motion';
import EventCard from './EventCard';
import { events } from '@/constants/events';
const UpcomingEvents =() =>{
    const [activeFilter, setActiveFilter] = useState<'all' | 'weekdays' | 'popular' | 'latest'>('all');

    // فلترة الأحداث حسب النوع المحدد
    const filteredEvents = events.filter(event => {
        if (activeFilter === 'all') return true;
        if (activeFilter === 'weekdays') return ['1', '2', '6'].includes(event.id);
        if (activeFilter === 'popular') return ['1', '4'].includes(event.id);
        if (activeFilter === 'latest') return ['3', '5'].includes(event.id);
        return true;
    });

    return (
        <section className="py-12 bg-gray-50">
            <div className="container mx-auto px-4">
                {/* العنوان وأزرار الفلترة */}
                <div className="flex flex-col md:flex-row justify-between items-center mb-8">
                    <h2 className="text-2xl font-bold text-gray-800 mb-4 md:mb-0">Upcoming Events</h2>
                    <div className="flex gap-2">
                        {['All','Weekdays', 'Popular', 'Latest'].map((filter) => {
                            const filterKey = filter.toLowerCase() as 'all' | 'weekdays' | 'popular' | 'latest';
                            return (
                                <motion.button
                                    key={filter}
                                    whileHover={{ scale: 1.05 }}
                                    whileTap={{ scale: 0.95 }}
                                    onClick={() => setActiveFilter(filterKey)}
                                    className={`px-4 py-2 rounded-full text-sm font-medium ${activeFilter === filterKey
                                            ? 'bg-purple-600 text-white'
                                            : 'bg-white text-gray-600 border border-gray-200'
                                        }`}
                                >
                                    {filter}
                                </motion.button>
                            );
                        })}
                    </div>
                </div>

                {/* شبكة البطاقات */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
                    {filteredEvents.map((event) => (
                        <EventCard key={event.id} event={event} />
                    ))}
                </div>

                {/* زر عرض الكل */}
                <div className="text-center">
                    <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        className="bg-purple-600 text-white px-6 py-3 rounded-full text-sm font-medium"
                    >
                        See All Events
                    </motion.button>
                </div>
            </div>
        </section >
    );
}
export default UpcomingEvents;