"use client";
import Image from "next/image";
import Link from "next/link";
import { motion } from "framer-motion";
import SearchFilter from "@/components/ui/SearchFilter";
import Slider from "../home/Slider";
export default function HeroSection() {
    return (
        <section className="relative h-[70vh] min-h-[570px] bg-gradient-to-r from-purple-400 to-pink-600 flex items-center justify-center">
            <Slider />
            {/* المحتوى المركزي مع تأثيرات الظهور */}
            <motion.div
                initial={{ opacity: 0, y: 20 }} // حالة البدء
                animate={{ opacity: 1, y: 0 }} // حالة النهاية
                transition={{ delay: 0.5 }} // تأخير الحركة
                className=" absolute z-10 px-4 text-center"
            >
                {/* العنوان الرئيسي مع تدرج لوني */}
                <h1 className="text-xl md:text-3xl font-bold mb-6 text-white">
                    Mavisuru Ragasoba
                </h1>

                {/* النص الوصفي */}
                <p className="text-sm md:text-xl text-gray-200 mb-8 max-w-2xl mx-auto">
                    Wander Girls 2000 Wonder Girls World Tour Star Purchase
                </p>

                {/* زر Learn More مع تأثيرات الـ hover */}
                <motion.div
                    whileHover={{ scale: 1.05 }} // تكبير عند المرور
                    whileTap={{ scale: 0.95 }} // تصغير عند النقر
                >
                    <Link
                        href="about"
                        className="inline-block px-8 py-3 border-2 border-purple-500 text-purple-100 rounded-full hover:bg-purple-500/20 transition-all duration-300"
                    >
                        Learn More →
                    </Link>
                </motion.div>
            </motion.div>
            <SearchFilter className="z-30 bg-white absolute bottom-24" />
        </section>
    );
}